  406: 'è¯·æ±çæ ¼å¼ä¸å¯å¾ã',
  410: 'è¯·æ±çèµæºè¢«æ°¸ä¹å é¤ï¼ä¸ä¸ä¼åå¾å°çã',
  422: 'å½åå»ºä¸ä¸ªå¯¹è±¡æ¶ï¼åçä¸ä¸ªéªè¯éè¯¯ã',
